.my-4 {
  margin-top:1rem;
  margin-bottom:1rem
}
.-mt-16 {
  margin-top:-4rem
}
.mr-4 {
  margin-right:1rem
}
.flex {
  display:flex
}
.h-\[200px\] {
  height:200px
}
.h-screen {
  height:100vh
}
.flex-col {
  flex-direction:column
}
.items-center {
  align-items:center
}
.justify-center {
  justify-content:center
}
.rounded {
  border-radius:.25rem
}
.bg-green-500 {
  --tw-bg-opacity: 1;
  background-color:rgb(34 197 94 / var(--tw-bg-opacity));
}
.bg-red-500 {
  --tw-bg-opacity: 1;
  background-color:rgb(239 68 68 / var(--tw-bg-opacity))
}
.px-4 {
  padding-left:1rem;
  padding-right:1rem
}
.py-2 {
  padding-top:.5rem;
  padding-bottom:.5rem
}
.text-4xl {
  font-size:2.25rem;
  line-height:2.5rem
}
.font-bold {
  font-weight:700
}
.text-white {
  --tw-text-opacity: 1;
  color:rgb(255 255 255 / var(--tw-text-opacity))
}
.hover\:bg-green-700:hover {
  --tw-bg-opacity: 1;
  background-color:rgb(21 128 61 / var(--tw-bg-opacity))
}
.hover\:bg-red-700:hover {
  --tw-bg-opacity: 1;
  background-color:rgb(185 28 28 / var(--tw-bg-opacity))
}
